import { Badge, Col, Row } from 'react-bootstrap';
import IconUI from '../../../components/IconUI/IconUI';
import './contest.scss';
const baseUrl = 'http://46.101.148.167';
const Contest = ({ contest }) => {
  const {
    title,
    detail,
    price,
    currency,
    slug,
    user,
    category,
    status,
    round,
    created_date,
    finish_date,
    is_locked,
    is_guaranteed,
    is_hidden,
    is_private,
    timesince,
    last_feedback_timesince,
    feedback_ratio,
    rate_ratio,
    user_number_of_contests,
    contest_image,
    remaining_time,
    entry_count,
    prize,
    currency_symbol
  } = contest;
  return (
    <div className='contest'>
      <Row>
        <Col md='auto'>
          <div className='card-ui'>
            <span className='price'>
              {currency_symbol}
              {prize}
            </span>
            {contest_image ? (
              <img src={baseUrl + contest_image} alt='img' />
            ) : (
              <>
                <IconUI
                  icon={
                    is_hidden
                      ? 'eye_slash_grey'
                      : is_private
                      ? 'lock_grey'
                      : 'no_design'
                  }
                />
                <span className='card-desc'>
                  {is_hidden ? 'Blind' : is_private ? 'Private' : 'No Design'}
                </span>
              </>
            )}
          </div>
        </Col>
        <Col className='mt-4 mt-md-0'>
          <Row>
            <Col lg={8} md={12}>
              <h5>{title}</h5>
              <p>{detail}</p>
              <Row>
                {is_guaranteed && (
                  <Col xs='auto' className='badge-col pe-0 mb-3'>
                    <Badge className='bg-pink'>
                      <IconUI icon='price' />
                      Guaranteed
                    </Badge>
                  </Col>
                )}
                {is_hidden && (
                  <Col xs='auto' className='badge-col pe-0 mb-3'>
                    <Badge className='bg-pink'>
                      <IconUI icon='eye_slash_red' />
                      Blind
                    </Badge>
                  </Col>
                )}
                {is_private && (
                  <Col xs='auto' className='badge-col pe-0 mb-3'>
                    <Badge className='bg-pink'>
                      <IconUI icon='lock_red' />
                      Private
                    </Badge>
                  </Col>
                )}
                <Col xs='auto' className='badge-col pe-0 mb-3'>
                  <Badge className='bg-grey'>Logo design</Badge>
                </Col>
              </Row>
            </Col>
            <Col className='right-section' lg={4} md={12}>
              <Row>
                <Col className='mb-3' lg={12} md='auto'>
                  <IconUI icon='design' />
                  {entry_count} designs
                </Col>

                <Col className='mb-3' lg={12} md='auto'>
                  <IconUI icon='timer' />
                  {remaining_time}
                </Col>

                <Col className='mb-3' lg={12} md='auto'>
                  <IconUI icon='feedback' />
                  {feedback_ratio}% feedback ({user_number_of_contests})
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default Contest;
