import clsx from 'clsx';
import { find } from 'lodash';
import { useState } from 'react';
import { Card, Form } from 'react-bootstrap';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import IconUI from '../../../components/IconUI/IconUI';
import { secondStepApi } from './onboardingApi';
import './step-2.scss';

const Step2 = ({ onNext, onError }) => {
  const [options, setOptions] = useState([
    {
      id: 1,
      name: 'I’m a customer',
      selected: true,
      image: 'img1',
      value: 'customer'
    },
    {
      id: 2,
      name: 'I’m a designer',
      image: 'img2',
      value: 'designer'
    },
    {
      id: 3,
      name: 'I’m a developer',
      image: 'img3',
      value: 'developer'
    }
  ]);
  const onSaveClick = () => {
    onError(null);
    const role = find(options, 'selected');
    secondStepApi(role.value)
      .then(res => onNext())
      .catch(({ response }) => onError(response.data));
  };
  const handleCardSelection = item => {
    setOptions(ops => ops.map(o => ({ ...o, selected: item.id === o.id })));
  };
  return (
    <div className='step-2'>
      <h2 className='mb-3 text-center'>What brings you to Zignative?</h2>
      <p className='d-block mb-5 pb-0 pb-lg-3 text-center'>
        Select the options that best describes you.
      </p>
      <div className='cards-section'>
        {options.map(item => (
          <Card
            className={clsx({ 'card-selected': item.selected })}
            onClick={() => handleCardSelection(item)}
            key={item.id}
          >
            <IconUI icon={item.image} type='png' />
            <Card.Body>
              <h5>{item.name}</h5>
              <Form.Check
                checked={item.selected}
                type='radio'
                id={`default-${item.id}`}
              />
            </Card.Body>
          </Card>
        ))}
      </div>
      <div className='d-flex justify-content-center'>
        <ButtonUI onClick={onSaveClick} className='next-btn'>
          Next
        </ButtonUI>
      </div>
    </div>
  );
};

export default Step2;
