import clsx from 'clsx';
import './upload-button.scss';
import ButtonUI from './ButtonUI';
const UploadButton = ({ onClick, className, children, variant, onChange }) => {
  return (
    <ButtonUI
      onClick={onClick}
      className={clsx('upload-button', className)}
      variant={variant}
    >
      {children}
      <input onChange={onChange} type='file' name='file' />
    </ButtonUI>
  );
};

export default UploadButton;
