import { Col, Nav, Row } from 'react-bootstrap';
import { useMediaQuery } from 'react-responsive';
import ContainerUI from '../ContainerUI/ContainerUI';
import IconUI from '../IconUI/IconUI';
import './footer.scss';
const Footer = () => {
  const isMobile = useMediaQuery({ maxWidth: 767 });
  const currentYear = new Date();
  return (
    <footer className='footer'>
      <ContainerUI>
        <Row>
          <Col sm={12} md='auto' className='order-2 order-md-1'>
            © {currentYear.getFullYear()} Zignative. All rights reserved.
          </Col>
          <Col className='order-1 order-md-1 mb-3 mb-md-0'>
            <Row className='justify-content-center'>
              <Col xs='auto'>
                <Nav.Link href=''>
                  {isMobile ? 'Terms' : 'Terms & Conditions'}
                </Nav.Link>
              </Col>
              <Col xs='auto'>
                <Nav.Link href=''>
                  {isMobile ? 'Privacy' : 'Privacy Policy'}
                </Nav.Link>
              </Col>
            </Row>
          </Col>
          <Col
            sm={12}
            md='auto'
            className='social-links order-0 order-md-1 mb-3 mb-md-0'
          >
            <Nav.Link className='pe-4 d-inline-block' href='#'>
              <IconUI icon='facebook' />
            </Nav.Link>
            <Nav.Link className='pe-4 d-inline-block' href='#'>
              <IconUI icon='twitter' />
            </Nav.Link>
            <Nav.Link className='pe-0 d-inline-block' href='#'>
              <IconUI icon='linkedin' />
            </Nav.Link>
          </Col>
        </Row>
      </ContainerUI>
    </footer>
  );
};

export default Footer;
