import clsx from 'clsx';
import { Form } from 'react-bootstrap';
import './checkbox-ui.scss';
const CheckboxUI = ({ feedback, label, className }) => {
  return (
    <Form.Group className={clsx('checkbox-ui', className)}>
      <Form.Check required label={label} feedback={feedback} />
    </Form.Group>
  );
};

export default CheckboxUI;
