import clsx from 'clsx';
import { Form } from 'react-bootstrap';
import './input-field.scss';
const InputField = ({
  placeholder,
  label,
  type = 'text',
  className,
  autoComplete,
  feedback,
  rightLabel,
  mutedText,
  value,
  onChange,
  name,
  isInvalid
}) => {
  return (
    <Form.Group className={clsx('input-field', className)}>
      {label && (
        <Form.Label className='w-100'>
          {label}
          {rightLabel}
        </Form.Label>
      )}
      <Form.Control
        autoComplete={autoComplete}
        type={type}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        name={name}
        isInvalid={isInvalid}
      />
      <Form.Control.Feedback className='mt-1' type='invalid'>
        {feedback}
      </Form.Control.Feedback>
      {mutedText && <Form.Text className='mt-2 d-block'>{mutedText}</Form.Text>}
    </Form.Group>
  );
};

export default InputField;
