import { useEffect, useState } from 'react';
import { ButtonGroup, Col, Form, Row, ToggleButton } from 'react-bootstrap';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import DropdownUI from '../../../components/DropdownUI/DropdownUI';
import IconUI from '../../../components/IconUI/IconUI';
import './filter.scss';
import { useMediaQuery } from 'react-responsive';
import { Element } from 'react-scroll';
import { categoriesApi } from './contestApi';
import { find } from 'lodash';

const Filter = ({
  onCategoryChange,
  onTimeFilterChange,
  onFilterChange,
  contests
}) => {
  const [showFilter, setShowFilter] = useState(false);
  const [statusValue, setStatusValue] = useState('');
  const [prizeMin, setPrizeMin] = useState('');
  const [prizeMax, setPrizeMax] = useState('');
  const [blindValue, setBlindValue] = useState('');
  const [guaranteedValue, setGuaranteedValue] = useState('');
  const [privateValue, setPrivateValue] = useState('');
  const [daysLeftValue, setDaysLeftValue] = useState('');
  const isMobile = useMediaQuery({ maxWidth: 767 });
  const [hover, setHover] = useState({});
  const isTablet = useMediaQuery({ maxWidth: 1024 });
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    categoriesApi().then(res => setCategories(res));
  }, []);

  useEffect(() => {
    onFilterChange({
      statusValue,
      blindValue,
      guaranteedValue,
      privateValue,
      daysLeftValue,
      prizeMin,
      prizeMax
    });
  }, [
    statusValue,
    blindValue,
    guaranteedValue,
    privateValue,
    daysLeftValue,
    prizeMin,
    prizeMax
  ]);
  const [timeFilter, setTimeFilter] = useState([
    { id: 1, name: 'Newest first', selected: true, value: 'title' },
    { id: 2, name: 'Oldest first', value: '-title' },
    { id: 3, name: 'Highest prize first', value: 'prize' },
    { id: 4, name: 'Lowest prize first', value: '-prize' },
    { id: 5, name: 'Most time left first', value: 'created_date' },
    { id: 6, name: 'Least time left first', value: '-created_date' },
    { id: 7, name: 'Most entries first', value: 'entry_count' },
    { id: 8, name: 'Least entries first', value: '-entry_count' }
  ]);

  const status = [
    { name: 'In progress', value: '1' },
    { name: 'Finished', value: '2' }
  ];

  const blind = [
    { icon: 'eye', value: '1' },
    { icon: 'eye_slash', value: '2' }
  ];

  const guaranteed = [
    { icon: 'dollar', value: '3' },
    { icon: 'dollar_slash', value: '4' }
  ];

  const privateList = [
    { icon: 'lock', value: '5' },
    { icon: 'unlock', value: '6' }
  ];

  const daysLeft = [
    { name: '<1', value: '1' },
    { name: '1-2', value: '2' },
    { name: '2-3', value: '3' },
    { name: '3+', value: '4' }
  ];

  const handleCategoryChange = category => {
    const updatedCategories = categories.map(n => ({
      ...n,
      selected: n.id === category.id ? !category.selected : false
    }));
    setCategories(updatedCategories);
    const selectedCategory = find(updatedCategories, { selected: true });
    onCategoryChange(selectedCategory);
  };

  const handleTimeFilterChange = item => {
    const updatedFitlers = timeFilter.map(n => ({
      ...n,
      selected: n.id === item.id ? !item.selected : false
    }));
    setTimeFilter(updatedFitlers);
    const selectedTimeFilter = find(updatedFitlers, { selected: true });
    onTimeFilterChange(selectedTimeFilter);
  };

  const isActive = val => {
    return val && parseInt(val) ? 1 : 0;
  };
  useEffect(() => {
    if (!isTablet || !hover.value) return;
    setHover({});
  }, [hover]);

  const filterCount =
    isActive(statusValue) +
    isActive(blindValue) +
    isActive(guaranteedValue) +
    isActive(privateValue) +
    isActive(daysLeftValue);

  const renderDescription = () => (
    <p>Showing {contests?.results?.length} contests (In progress)</p>
  );
  const selectedTimeFilter = find(timeFilter, { selected: true });
  const renderTimeFilter = () => (
    <Col xs='auto' className=''>
      <DropdownUI
        title={selectedTimeFilter ? selectedTimeFilter.name : 'Newest first'}
        list={timeFilter}
        onChange={handleTimeFilterChange}
      />
    </Col>
  );

  const renderFilter = () =>
    showFilter && (
      <div className='mt-0 mt-md-4 filter-list'>
        <div className='filter-item'>
          <Form.Label>Contest status</Form.Label>
          <ButtonGroup className='mb-4 w-100'>
            {status.map((item, idx) => (
              <ToggleButton
                key={idx}
                className='flex-grow-0'
                type='checkbox'
                id={`status-${idx}`}
                variant='outline-secondary'
                checked={statusValue === item.value}
                value={item.value}
                onChange={e =>
                  setStatusValue(e.currentTarget.checked && item.value)
                }
              >
                {item.name}
              </ToggleButton>
            ))}
          </ButtonGroup>
        </div>

        <div className='filter-item'>
          <Form.Label>Contest types</Form.Label>
          <Row>
            <Form.Group as={Col} className='mb-4'>
              <ButtonGroup as={Col} className=' pe-1'>
                {blind.map((item, idx) => (
                  <ToggleButton
                    key={idx}
                    type='checkbox'
                    id={`blind-${idx}`}
                    variant='outline-secondary'
                    checked={blindValue === item.value}
                    value={item.value}
                    onChange={e =>
                      setBlindValue(e.currentTarget.checked && item.value)
                    }
                    onMouseEnter={() => setHover(item)}
                    onMouseLeave={() => setHover({})}
                  >
                    <IconUI
                      icon={
                        hover.value === item.value || blindValue === item.value
                          ? `${item.icon}_white`
                          : item.icon
                      }
                    />
                  </ToggleButton>
                ))}
              </ButtonGroup>
              <Form.Text className='text-center'>Blind</Form.Text>
            </Form.Group>

            <Form.Group as={Col} className='mb-4'>
              <ButtonGroup as={Col} className=' pe-1'>
                {guaranteed.map((item, idx) => (
                  <ToggleButton
                    key={idx}
                    type='checkbox'
                    id={`guaranteed-${idx}`}
                    variant='outline-secondary'
                    checked={guaranteedValue === item.value}
                    value={item.value}
                    onChange={e =>
                      setGuaranteedValue(e.currentTarget.checked && item.value)
                    }
                    onMouseEnter={() => setHover(item)}
                    onMouseLeave={() => setHover({})}
                  >
                    <IconUI
                      icon={
                        hover.value === item.value ||
                        guaranteedValue === item.value
                          ? `${item.icon}_white`
                          : item.icon
                      }
                    />
                  </ToggleButton>
                ))}
              </ButtonGroup>
              <Form.Text className='text-center'>Guaranteed</Form.Text>
            </Form.Group>
            <Form.Group as={Col} className='mb-4'>
              <ButtonGroup as={Col} className=''>
                {privateList.map((item, idx) => (
                  <ToggleButton
                    key={idx}
                    type='checkbox'
                    id={`private-${idx}`}
                    variant='outline-secondary'
                    checked={privateValue === item.value}
                    value={item.value}
                    onChange={e =>
                      setPrivateValue(e.currentTarget.checked && item.value)
                    }
                    onMouseEnter={() => setHover(item)}
                    onMouseLeave={() => setHover({})}
                  >
                    <IconUI
                      icon={
                        hover.value === item.value ||
                        privateValue === item.value
                          ? `${item.icon}_white`
                          : item.icon
                      }
                    />
                  </ToggleButton>
                ))}
              </ButtonGroup>
              <Form.Text className='text-center'>Private</Form.Text>
            </Form.Group>
          </Row>
        </div>

        <div className='filter-item'>
          <Form.Label>Days left</Form.Label>
          <ButtonGroup className='mb-4'>
            {daysLeft.map((item, idx) => (
              <ToggleButton
                key={idx}
                type='checkbox'
                id={`days-left-${idx}`}
                className='flex-grow-0'
                variant='outline-secondary'
                checked={daysLeftValue === item.value}
                value={item.value}
                onChange={e =>
                  setDaysLeftValue(val =>
                    val === e.target.value ? '' : e.target.value
                  )
                }
              >
                {item.name}
              </ToggleButton>
            ))}
          </ButtonGroup>
        </div>

        <div className='filter-item'>
          <Form.Group as={Col} className='mb-4'>
            <Form.Label>Prize</Form.Label>
            <Row>
              <Col className='pe-1'>
                <Form.Control
                  className='prize-text'
                  type='text'
                  placeholder='$ Min'
                  value={prizeMin}
                  onChange={({ target: { value } }) => setPrizeMin(value)}
                />
              </Col>
              <Col>
                <Form.Control
                  className='prize-text'
                  type='text'
                  placeholder='$ Max'
                  value={prizeMax}
                  onChange={({ target: { value } }) => setPrizeMax(value)}
                />
              </Col>
            </Row>
          </Form.Group>
        </div>
      </div>
    );

  const selectedCategory = find(categories, { selected: true });

  return (
    <div className='filter'>
      <h2>Browse contests</h2>
      {!isMobile && renderDescription()}
      <Row>
        <Col md='auto' sm='12'>
          <DropdownUI
            list={categories.map(n => ({ ...n, name: n.title }))}
            title={selectedCategory ? selectedCategory.title : 'All categories'}
            onChange={handleCategoryChange}
          />
        </Col>

        <Col
          md='auto'
          sm='12'
          className='flex-grow-1 mt-md-0 mt-3 mb-md-0 mb-3'
        >
          <ButtonUI
            className={filterCount && 'label-active'}
            onClick={() => setShowFilter(s => !s)}
            variant='outline-secondary'
          >
            {filterCount ? (
              <span className='filter-number'>{filterCount}</span>
            ) : (
              <IconUI icon='filter' />
            )}
            &nbsp; Filters
          </ButtonUI>
        </Col>
        {isMobile && renderFilter()}

        {isMobile && renderDescription()}
        {renderTimeFilter()}
        {!isMobile && renderFilter()}
      </Row>
    </div>
  );
};

export default Filter;
