import { capitalize } from 'lodash';
import { Fragment } from 'react';
import SnackbarUI from './SnackbarUI';
const ErrorSnackbar = ({ errors }) => {
  return (
    errors && (
      <SnackbarUI
        message={
          <div className='text-start m-auto d-table'>
            {Object.keys(errors).map(key => (
              <Fragment key={key}>
                {capitalize(key)}: {errors[key]}
                <br />
              </Fragment>
            ))}
          </div>
        }
        type='error'
      />
    )
  );
};

export default ErrorSnackbar;
