import axios from 'axios';
import { useQuery } from 'react-query';
const filterResult = (filter, name) => {
  return filter[name] ? `&${name}=${filter[name]}` : '';
};
export const useContestsApi = ({
  page,
  selectedCategory,
  selectedFilter,
  selectedTimeFilter
}) => {
  const filter = {
    category: selectedCategory?.id,
    ordering: selectedTimeFilter?.value,
    status:
      selectedFilter?.statusValue === '1'
        ? 'in_progress'
        : selectedFilter?.statusValue === '2'
        ? 'completed'
        : null,
    is_hidden:
      selectedFilter?.blindValue === '1'
        ? 'true'
        : selectedFilter?.blindValue === '2'
        ? 'false'
        : null,
    is_guaranteed:
      selectedFilter?.guaranteedValue === '3'
        ? 'true'
        : selectedFilter?.guaranteedValue === '4'
        ? 'false'
        : null,
    is_private:
      selectedFilter?.privateValue === '5'
        ? 'true'
        : selectedFilter?.privateValue === '6'
        ? 'false'
        : null,
    prize_min: selectedFilter?.prizeMin,
    prize_max: selectedFilter?.prizeMax,
    days_left: selectedFilter?.daysLeftValue
  };

  const { isLoading, error, data } = useQuery(
    `data-${page}${JSON.stringify(selectedCategory)}${JSON.stringify(
      selectedFilter
    )}${JSON.stringify(selectedTimeFilter)}`,
    () =>
      axios
        .get(
          `/contest?page=${page}${filterResult(
            filter,
            'category'
          )}${filterResult(filter, 'ordering')}${filterResult(
            filter,
            'is_hidden'
          )}${filterResult(filter, 'status')}${filterResult(
            filter,
            'is_guaranteed'
          )}${filterResult(filter, 'is_private')}${filterResult(
            filter,
            'prize_min'
          )}${filterResult(filter, 'prize_max')}${filterResult(
            filter,
            'days_left'
          )}`
        )
        .then(res => res)
  );

  return { isLoading, error, data };
};

export const categoriesApi = () => {
  return axios.get(`/all-categories`).then(data => data);
};
