import clsx from 'clsx';
import { Dropdown, DropdownButton } from 'react-bootstrap';
import './dropdown-ui.scss';
const DropdownUI = ({ title, list = [], className, onChange }) => {
  return (
    <DropdownButton
      variant='outline-secondary'
      className={clsx('dropdown-ui', className)}
      title={title}
    >
      {list.map(item => (
        <Dropdown.Item
          onClick={() => onChange(item)}
          key={item.id}
          className={clsx({
            active: item.selected
          })}
        >
          {item.name}
        </Dropdown.Item>
      ))}
    </DropdownButton>
  );
};

export default DropdownUI;
