import clsx from 'clsx';
import { useState } from 'react';
import { useHistory } from 'react-router';
import ContainerUI from '../../components/ContainerUI/ContainerUI';
import Header from '../../components/Header/Header';
import ErrorSnackbar from '../../components/SnackbarUI/ErrorSnackbar';
import Step1 from './components/Step1';
import Step2 from './components/Step2';
import './on-boarding.scss';

const OnBoarding = () => {
  const [errors, setErrors] = useState(null);
  const [activeStep, setActiveStep] = useState(1);
  const history = useHistory();
  return (
    <div className='on-boarding-page'>
      <ErrorSnackbar errors={errors} />

      <Header authScreen onBoarding></Header>
      <ContainerUI
        className={clsx('my-5 content', `active-content-${activeStep}`)}
      >
        {activeStep === 1 && (
          <Step1 onError={setErrors} onNext={() => setActiveStep(2)} />
        )}
        {activeStep === 2 && (
          <Step2 onError={setErrors} onNext={() => history.push('/')} />
        )}
      </ContainerUI>
    </div>
  );
};

export default OnBoarding;
