import { Nav, Navbar } from 'react-bootstrap';
import { useHistory } from 'react-router';
import ContainerUI from '../ContainerUI/ContainerUI';
import IconUI from '../IconUI/IconUI';
import './header.scss';

const Header = ({ authScreen, register, login, onBoarding }) => {
  const history = useHistory();
  return (
    <header className='header'>
      <ContainerUI fluid>
        <Navbar bg='' expand='lg' className='justify-content-between'>
          {!authScreen && <Navbar.Toggle />}
          {authScreen && (
            <IconUI
              onClick={() => history.goBack()}
              className='d-inline-block d-lg-none pointer'
              icon='back'
            />
          )}
          <Navbar.Brand href='/'>
            <IconUI icon='Logo' />
          </Navbar.Brand>
          {onBoarding && (
            <Nav.Item className='d-none d-lg-flex align-items-center'>
              <Nav.Link className='color-blue' href='/login'>
                Sign out
              </Nav.Link>
            </Nav.Item>
          )}
          {authScreen && <div className='d-inline-block d-lg-none'></div>}
          {login && (
            <Nav.Item className='d-none d-lg-flex align-items-center'>
              Not a member?
              <Nav.Link className='color-blue' href='/register'>
                Sign up now
              </Nav.Link>
            </Nav.Item>
          )}

          {register && (
            <Nav.Item className='d-none d-lg-flex align-items-center'>
              Already a member?
              <Nav.Link className='color-blue' href='/login'>
                Sign in
              </Nav.Link>
            </Nav.Item>
          )}

          {!authScreen && (
            <>
              <Navbar.Collapse>
                <Nav className='mr-auto'>
                  <Nav.Link href='/how-it-works'>How it works</Nav.Link>
                  <Nav.Link href='/service'>Service</Nav.Link>
                  <Nav.Link href='/contests'>Contests</Nav.Link>
                  <Nav.Link href='/blog'>Blog</Nav.Link>
                </Nav>
              </Navbar.Collapse>
              <Nav variant='pills' className='justify-content-end' activeKey=''>
                <Nav.Item className='d-md-block d-none me-3'>
                  <Nav.Link href='/login'>Sign in</Nav.Link>
                </Nav.Item>
                <Nav.Item className='d-lg-block d-none'>
                  <Nav.Link
                    active
                    className='btn btn-primary button-ui'
                    href='/register'
                  >
                    Sign up
                  </Nav.Link>
                </Nav.Item>
              </Nav>
            </>
          )}
        </Navbar>
      </ContainerUI>
    </header>
  );
};

export default Header;
