import { Col, Row } from 'react-bootstrap';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import IconUI from '../../../components/IconUI/IconUI';
import './banner.scss';
const Banner = () => {
  return (
    <Row className='banner'>
      <Col xs={12} md={8} lg={6} className='left-section'>
        <h1>Take your business online today</h1>
        <p>
          Business everywhere are moving more quickly than ever before. Our
          creative platform allows your brand to evolve fast and tap into new
          digital strategies.
        </p>
        <ButtonUI>Start a contest - $149</ButtonUI>
      </Col>
      <Col className='d-lg-block d-none'>
        <IconUI icon='brand' type='png' />
      </Col>
    </Row>
  );
};

export default Banner;
