import { useFormik } from 'formik';
import { useState } from 'react';
import { Form, Nav } from 'react-bootstrap';
import { Link, useHistory } from 'react-router-dom';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import GoogleLoginButton from '../../../components/ButtonUI/GoogleLoginButton';
import CheckboxUI from '../../../components/CheckboxUI/CheckboxUI';
import ContainerUI from '../../../components/ContainerUI/ContainerUI';
import Header from '../../../components/Header/Header';
import InputField from '../../../components/InputField/InputField';
import SnackbarUI from '../../../components/SnackbarUI/SnackbarUI';
import * as Yup from 'yup';
import Auth from '../Auth';
import './register.scss';
import { registerApi } from './registerApi';
import { Fragment } from 'react';
import { capitalize } from 'lodash';
import ErrorSnackbar from '../../../components/SnackbarUI/ErrorSnackbar';
const RegisterSchema = Yup.object().shape({
  password: Yup.string().required('Password Required'),
  email: Yup.string().email().required('Email Required'),
  username: Yup.string().required('Username Required')
});

const Register = () => {
  const [showEmailActivation, setShowEmailActivation] = useState(false);
  const [errors, setErrors] = useState(false);
  const history = useHistory();
  const responseGoogle = response => {
    console.log(response);
  };
  const handleSubmit = values => {
    setErrors(null);

    registerApi(values)
      .then(() => {
        setShowEmailActivation(true);
      })
      .catch(({ response }) => {
        setErrors(response.data);
      });
    // setShowEmailActivation(true);
    // setTimeout(() => {
    //   history.push('/on-boarding');
    // }, 5000);
  };
  const formik = useFormik({
    // validationSchema: RegisterSchema,
    // validateOnBlur: true,
    initialValues: {
      password: '',
      email: '',
      username: ''
    },
    onSubmit: values => {
      handleSubmit(values);
    }
  });
  return (
    <Auth className='register-page'>
      <Header authScreen register></Header>
      {/* {showEmailActivation && (
        <SnackbarUI
          message='Your account has been activated! Now you can use your account.'
          type='success'
        />
      )} */}

      {showEmailActivation && (
        <SnackbarUI
          message='Your account has been register! Now you will recieve an email to confirm your account.'
          type='info'
        />
      )}
      <ErrorSnackbar errors={errors} />
      <ContainerUI className='my-5 auth-form'>
        <h2 className='text-center mb-4'>Sign up to Zignative</h2>
        <Form>
          <GoogleLoginButton
            className='w-100 mb-4'
            buttonText='Sign up with Google'
            onSuccess={responseGoogle}
            onFailure={responseGoogle}
          />
          <div className='hr'>
            <span>Or</span>
          </div>

          <InputField
            // autoComplete='username'
            className='mb-4'
            name='username'
            label='Username'
            isInvalid={errors?.username}
            value={formik.values.username}
            onChange={formik.handleChange}
          />
          <InputField
            // autoComplete='new-email'
            className='mb-4'
            name='email'
            label='Email Address'
            isInvalid={errors?.email}
            value={formik.values.email}
            onChange={formik.handleChange}
          />
          <InputField
            // autoComplete='new-password'
            className='mb-4'
            label='Password'
            name='password'
            type='password'
            isInvalid={errors?.password}
            value={formik.values.password}
            onChange={formik.handleChange}
          />
          <CheckboxUI
            className='mb-4'
            label={
              <>
                Creating an account means you’re okay with our&nbsp;
                <Link className='link' to='/'>
                  Terms of Service
                </Link>
                ,&nbsp;
                <Link className='link' to='/'>
                  Privacy Policy
                </Link>
                , and our default&nbsp;
                <Link className='link' to='/'>
                  Notification Settings
                </Link>
                .
              </>
            }
          />
          <ButtonUI onClick={formik.handleSubmit} className='w-100'>
            Create Account
          </ButtonUI>
          <div className='d-flex d-lg-none align-items-center justify-content-center mt-4'>
            Already a member?&nbsp;
            <Link className='link' to='/login'>
              Sign in
            </Link>
          </div>
          <Form.Text className='text-muted mt-4 pt-2 d-block'>
            This site is protected by reCAPTCHA and the Google&nbsp;
            <Link className='link' to='/'>
              Privacy Policy
            </Link>
            &nbsp;and&nbsp;
            <Link className='link' to='/'>
              Terms of Service
            </Link>
            &nbsp;apply.
          </Form.Text>
        </Form>
      </ContainerUI>
    </Auth>
  );
};

export default Register;
