import axios from 'axios';

export const firstStepApi = (image, country) => {
  const formData = new FormData();
  formData.append('image', image);
  formData.append('country', country);
  const config = { headers: { 'Content-Type': 'multipart/form-data' } };
  return axios.post('user/register/step_2', formData, config);
};

export const secondStepApi = role => {
  return axios.post('user/register/step_3', { role });
};
