import clsx from 'clsx';
import { useState } from 'react';
import { Col, Row } from 'react-bootstrap';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import UploadButton from '../../../components/ButtonUI/UploadButton';
import InputField from '../../../components/InputField/InputField';
import ErrorSnackbar from '../../../components/SnackbarUI/ErrorSnackbar';
import { firstStepApi } from './onboardingApi';
import './step-1.scss';

const Step1 = ({ onNext, onError }) => {
  const [imgFile, setImgFile] = useState(null);
  const [location, setLocation] = useState('');
  const handleFileUpload = e => {
    const [file] = e.target.files;
    if (file) {
      setImgFile(file);
    }
  };
  const onSaveClick = () => {
    onError(null);
    firstStepApi(imgFile, location)
      .then(res => onNext())
      .catch(
        ({ response }) => console.log(response)
        // onError(response.data)
      );
  };
  return (
    <div className='step-1'>
      <h2 className='mb-3'>Welcome! Let’s create your profile</h2>
      <p className='d-block mb-4 pb-0 pb-lg-2 d-block'>
        Let other get to you know better! You can do these later
      </p>
      <h4>Add an avatar</h4>
      <div className='avatar-section'>
        <div
          className={clsx('avatar-image-wrap me-md-5 me-0 mb-md-5 mb-0', {
            'is-image': imgFile
          })}
        >
          {imgFile && (
            <img
              className='avatar-img'
              src={URL.createObjectURL(imgFile)}
              alt='user-profile'
            />
          )}
        </div>
        <div>
          <UploadButton onChange={handleFileUpload} variant='outline-secondary'>
            Choose image
          </UploadButton>
          <ButtonUI className='skip-btn mt-4 default-img-btn' variant=''>
            Or choose one of our defaults
          </ButtonUI>
        </div>
      </div>
      <h4>Add your location</h4>
      <InputField
        value={location}
        onChange={({ target: { value } }) => setLocation(value)}
        className='location-form'
        placeholder='Enter a location'
      />
      <Row className='mt-4 pt-2'>
        <Col>
          <ButtonUI onClick={onSaveClick} className='next-btn'>
            Next
          </ButtonUI>
        </Col>
        <Col xs='auto'>
          <ButtonUI onClick={onNext} className='skip-btn' variant=''>
            Skip
          </ButtonUI>
        </Col>
      </Row>
    </div>
  );
};

export default Step1;
