import clsx from 'clsx';
import './snackbar-ui.scss';
const SnackbarUI = ({ message, type, className }) => {
  return (
    <div
      className={clsx(
        'snackbar-ui',
        {
          success: type === 'success',
          error: type === 'error',
          info: type === 'info'
        },
        className
      )}
    >
      {message}
    </div>
  );
};

export default SnackbarUI;
