import { useState } from 'react';
import { Form, FormLabel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import ContainerUI from '../../../components/ContainerUI/ContainerUI';
import Header from '../../../components/Header/Header';
import InputField from '../../../components/InputField/InputField';
import ErrorSnackbar from '../../../components/SnackbarUI/ErrorSnackbar';
import SnackbarUI from '../../../components/SnackbarUI/SnackbarUI';
import Auth from '../Auth';
import './forgot-password.scss';
import { forgotApi } from './forgotApi';

const ForgotPassword = () => {
  const [showMessage, setShowMessage] = useState(false);
  const [errors, setErrors] = useState(false);
  const [email, setEmail] = useState('');
  const onSubmit = () => {
    forgotApi({ email })
      .then(res => setShowMessage(true))
      .catch(({ response }) => setErrors(response.data));
  };
  return (
    <Auth className='forgot-password-page'>
      <Header authScreen login></Header>
      <ErrorSnackbar errors={errors} />
      {showMessage && (
        <SnackbarUI
          type='success'
          message={'Instructions has been sent on email'}
        />
      )}
      <ContainerUI className='my-5 auth-form'>
        <h2 className='mb-4'>Forgot Password?</h2>
        <Form>
          <Form.Text className='d-block mb-4'>
            <>
              Enter the email address you used when you joined and we’ll send
              you instructions to reset your password.
              <br />
              <br />
              For security reasons, we do NOT store your password. So rest
              assured that we will never send your password via email.
            </>
          </Form.Text>
          <InputField
            autoComplete='new-password'
            className='mb-4'
            label='Email Address'
            type='email'
            value={email}
            onChange={({ target: { value } }) => setEmail(value)}
          />

          <ButtonUI onClick={onSubmit} className='w-100'>
            Send Reset Instructions
          </ButtonUI>

          <div className='d-flex d-lg-none align-items-center justify-content-center mt-4'>
            Not a member?&nbsp;
            <Link className='link' href='/register'>
              Sign up now
            </Link>
          </div>
        </Form>
      </ContainerUI>
    </Auth>
  );
};

export default ForgotPassword;
