import { range } from 'lodash';
import Contest from './Contest';
import Filter from './Filter';
import Pagination from './Pagination';
import './contests.scss';
import { useEffect, useState } from 'react';
import { useContestsApi } from './contestApi';
import Loading from '../../../components/Loading/Loading';

const Contests = () => {
  // const [contests, setContests] = useState(null);
  const [page, setPage] = useState(1);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [selectedTimeFilter, setSelectedTimeFilter] = useState(null);
  const [selectedFilter, setSelectedFilter] = useState(null);
  const {
    isLoading,
    error,
    data: contests
  } = useContestsApi({
    page,
    selectedCategory,
    selectedTimeFilter,
    selectedFilter
  });

  return (
    <div className='contests'>
      <Filter
        onTimeFilterChange={setSelectedTimeFilter}
        onCategoryChange={setSelectedCategory}
        onFilterChange={setSelectedFilter}
        contests={contests}
      />
      {error && <div className='my-5 text-center'>Something Went Wrong</div>}
      {isLoading && <Loading />}
      {!isLoading && !contests?.results?.length && (
        <center className='my-5'>No Record Found</center>
      )}
      {contests?.results?.map(item => (
        <Contest key={item.created_date} contest={item} />
      ))}
      <Pagination
        next={contests?.next}
        previous={contests?.previous}
        onNextClick={() => setPage(page => page + 1)}
        onPreviousClick={() => setPage(page => page - 1)}
      />
    </div>
  );
};

export default Contests;
