import clsx from 'clsx';
import GoogleLogin from 'react-google-login';
import './google-login-button.scss';
const GoogleLoginButton = ({ buttonText, onSuccess, onFailure, className }) => {
  return (
    <GoogleLogin
      className={clsx('google-login-button', className)}
      style={{ background: 'none' }}
      clientId='962330538777-5bpjak660pfmag0rcqq6bbbj8dho43v3.apps.googleusercontent.com'
      buttonText={buttonText}
      onSuccess={onSuccess}
      onFailure={onFailure}
      cookiePolicy={'single_host_origin'}
    />
  );
};

export default GoogleLoginButton;
