import axios from 'axios';

export const loginApi = (username, password) => {
  return axios.post('/token/', { username, password }).then(res => res);
};

export const verifyTokenApi = () => {
  return axios.get('/contest/1').then(res => res);
};

export const verifyEmailApi = link => {
  return axios.get(`/user/verify-account/${link}`);
};
