import clsx from 'clsx';
import { Button } from 'react-bootstrap';
import './button-ui.scss';
const ButtonUI = ({ onClick, className, children, variant }) => {
  return (
    <Button
      onClick={onClick}
      className={clsx('button-ui', className)}
      variant={variant}
    >
      {children}
    </Button>
  );
};

export default ButtonUI;
