import { useEffect, useState } from 'react';
import { Form } from 'react-bootstrap';
import { Link, useHistory, useLocation } from 'react-router-dom';
import ButtonUI from '../../../components/ButtonUI/ButtonUI';
import ContainerUI from '../../../components/ContainerUI/ContainerUI';
import Header from '../../../components/Header/Header';
import InputField from '../../../components/InputField/InputField';
import ErrorSnackbar from '../../../components/SnackbarUI/ErrorSnackbar';
import SnackbarUI from '../../../components/SnackbarUI/SnackbarUI';
import Auth from '../Auth';
import queryString from 'query-string';
import './reset-password.scss';
import { resetPasswordApi } from './resetPasswordApi';

const ResetPassword = () => {
  const [showMessage, setShowMessage] = useState(false);
  const [errors, setErrors] = useState(null);
  const [password, setPassword] = useState('');
  const search = useLocation().search;
  const history = useHistory();

  useEffect(() => {
    const parsed = queryString.parse(search);
    if (!parsed.token) history.push('/login');
  }, [search]);

  const onResetPassword = () => {
    const parsed = queryString.parse(search);
    if (parsed.token) {
      setErrors(null);
      setShowMessage(false);
      resetPasswordApi({ link: parsed.token, password })
        .then(res => {
          setShowMessage(true);
        })
        .catch(({ response }) => {
          setErrors(response.data);
        });
    }
  };
  return (
    <Auth className='reset-password-page'>
      <Header authScreen login></Header>
      {<ErrorSnackbar errors={errors} />}
      {showMessage && (
        <SnackbarUI type='success' message='Your password has been reseted!' />
      )}
      <ContainerUI className='my-5 auth-form'>
        <h2 className='mb-4'>Reset your password</h2>
        <Form>
          <InputField
            autoComplete='new-password'
            className='mb-4'
            label='New Password'
            type='password'
            mutedText='Minimum 6 characters'
            value={password}
            onChange={e => setPassword(e.target.value)}
          />

          <ButtonUI onClick={onResetPassword} className='w-100'>
            Reset Password
          </ButtonUI>

          <div className='d-flex d-lg-none align-items-center justify-content-center mt-4'>
            Not a member?&nbsp;
            <Link to='/register' className='link'>
              Sign up now
            </Link>
          </div>
        </Form>
      </ContainerUI>
    </Auth>
  );
};

export default ResetPassword;
